	module.exports = {"data":{"markdownRemark":{"html":"<h2>Git Alias</h2>\n<p>Git doesn’t automatically infer your command if you type it in partially. If you don’t want to type the entire text of each of the Git commands, you can easily set up an alias for each command using git config. Here are a couple of examples you may want to set up:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-shell\"><code>$ git config --global alias.co checkout\n$ git config --global alias.br branch\n$ git config --global alias.ci commit\n$ git config --global alias.st status</code></pre>\n      </div>\n<p>This means that, for example, instead of typing git commit, you just need to type git ci. As you go on using Git, you’ll probably use other commands frequently as well; don’t hesitate to create new aliases.</p>\n<p>This technique can also be very useful in creating commands that you think should exist. For example, to correct the usability problem you encountered with unstaging a file, you can add your own unstage alias to Git:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-shell\"><code>$ git config --global alias.unstage 'reset HEAD --'</code></pre>\n      </div>\n<p>This makes the following two commands equivalent:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-shell\"><code>$ git unstage fileA\n$ git reset HEAD fileA</code></pre>\n      </div>\n<p>This seems a bit clearer. It’s also common to add a last command, like this:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-shell\"><code>$ git config --global alias.last 'log -1 HEAD'</code></pre>\n      </div>\n<p>This way, you can see the last commit easily:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-shell\"><code>$ git last\ncommit 66938dae3329c7aebe598c2246a8e6af90d04646\nAuthor: Josh Goebel <dreamer3@example.com>\nDate:   Tue Aug 26 19:48:51 2008 +0800\n\n    test for current head\n\n    Signed-off-by: Scott Chacon <schacon@example.com></code></pre>\n      </div>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-shell\"><code>$ git config --global alias.st status --short --branch</code></pre>\n      </div>\n<p>When you run the command <code>git st</code>, your git status will be displayed in a nice, streamlined format.</p>\n<h3>View, Edit and Delete Aliases</h3>\n<p>To view all of the aliases you have created on your machine, run the command:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-shell\"><code>cat ~/.gitconfig</code></pre>\n      </div>\n<p>Replacing <code>cat</code> with <code>nano</code> will allow you to edit them or remove them completely.</p>","fields":{"slug":"/git/git-alias/"},"frontmatter":{"title":"Git Aliases"}}},"pathContext":{"slug":"/git/git-alias/"}}

